import React from 'react';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import TypoGraphy from '@material-ui/core/TypoGraphy';

const Home = () => (
  <div>
	<Grid containter direction='column' spacing={3} class='home-grid'>
		<Grid item style={{marginBottom:'1em'}}>
			<Card raised={true}>
				<CardHeader title={'About the Project'} titleTypographyProps={{variant:'h5', gutterBottom:false}} style={{backgroundColor:'#0073E6', color:'#FFFFFF'}}>About the Capstone Project</CardHeader>
					<CardContent style={{paddingLeft:5, paddingRight:5, paddingBottom:2, marginBottom: '1em'}}>
						<p>"The capstone project is focused on the application of principles and the practice of engineering and is not meant to be a mini-thesis. 
						The capstone projects provide a mechanism to demonstrate a synthesis of knowledge and the application of advanced concepts learned in class 
						to a specific problem. Students can apply the skills and knowledge acquired in the program to a known problem in order to develop an appropriate solution."</p>
						<a href='https://ceas.uc.edu/academics/degrees-programs/graduate/master-of-engineering.html'>Master of Enginerring Degree (MEng) - Graduate Programs | University of Cincinnati</a>
					</CardContent>
			</Card>
		</Grid>

		<Grid item style={{marginBottom:'1em'}}>
			<Card raised={true}>			
				<CardHeader title={'Problem Statement'} titleTypographyProps={{variant:'h5', gutterBottom:false}} style={{backgroundColor:'#0073E6', color:'#FFFFFF'}}>About the Problem</CardHeader>
				<CardContent style={{paddingLeft:5, paddingRight:5, paddingBottom:2}}>
					<p>Applied Behavioral Analysis (ABA) and Discrete Trial Therapy (DTT), implemented by Registered Behavior Technicians (RBT) and supervised by Behavior Analysts, 
					is an evidence-based intervention for the autism spectrum disorder. Therapy sessions consists of a collection of individualized programs that 
					teach new behaviors to compensate for deficits and excesses in behavior. RBT’s conduct learning “trials,” with the individual, often during one-on-one sessions, 
					and record the individual’s response for each trial. </p>
					<p>With dozens of trials conducted per session, and sessions occurring several times per month, the breadth of data collected can allow for more than program 
					goal modifications.</p>
				</CardContent>				
			</Card>
		</Grid>
		<Grid item style={{marginBottom:'1em'}}>
			<Card raised={true}>
				<CardHeader title={'Proposed Solution'} titleTypographyProps={{variant:'h5', gutterBottom:false}} style={{backgroundColor:'#0073E6', color:'#FFFFFF'}}>Proposed Solution</CardHeader>
					<CardContent style={{paddingLeft:5, paddingRight:5, paddingBottom:2}}>
						<p>Machine learning techniques may offer a solution for this data-driven field. An increasing number of industries and businesses are using the data science 
						approach to discover new insights within their data. As computing and technological capabilities have progressed, so too has the ability to process 
						large sets of data.</p>
						<p>Machine learning has been applied to problems in agricultural production, epidemiological research, and an array of commercial and financial improvements. 
						It is the predictive ability of machine learning that organizations have found to be of such great value. </p>
						<p>Choosing which programs to run, and in which order to run them, can be aided by the use of machine learning algorithms. This area of study, educational 
						data mining, is well-proven to both predict student achievement levels and increase the responsiveness of the educational experience. </p>
						<p>By mining past therapy sessions for student performance on programs, the order in which these programs were presented, and the number of trials per program per session, 
						machine learning techniques could offer suggestions for ways to implement future sessions.</p>
					</CardContent>
			</Card>
		</Grid>
		<Grid item style={{marginBottom:'1em'}}>
			<Card raised={true}>
				<CardHeader title={'Potential Data Insights'} titleTypographyProps={{variant:'h5', gutterBottom:false}} style={{backgroundColor:'#0073E6', color:'#FFFFFF'}}>Potential Data Insights</CardHeader>
				<CardContent style={{paddingLeft:5, paddingRight:5, paddingBottom:2}}>
					<Grid container direction='column' spacing={2} style={{marginLeft:'2em'}}>
						<Grid item xs={11}>
							<li>Is there a pattern of programs that lead to success or failure in a specific program for a particular student or set of students?</li>
						</Grid>
						<Grid item xs={11}>
							<li>Is there a pattern of programs that lead to successes or failures among all students?</li>
						</Grid>
						<Grid item xs={11}>
							<li>Across all students, are there certain programs that should precede or proceed from other programs?</li>
						</Grid>
						<Grid item xs={11}>
							<li>Given a student's past success rates on all programs, which program(s) does the client have the most chance for success when starting a therapeutic session?</li>
						</Grid>
						<Grid item xs={11}>
							<li>Based on the success or failure of that program, which program(s) does the client have the most change for success for the next given program?</li>
						</Grid>						
						<Grid item xs={11}>
							<li>Does the number of trials conducted in a specific program per session relate to the success or failure rate within that program? Within other programs?</li>
						</Grid>						
					</Grid>
				</CardContent>
			</Card>
		</Grid>
	</Grid>
  </div>
);

export default Home;