import React from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import TypoGraphy from '@material-ui/core/Typography'
import Home from './Home';
import About from './About';
import Contact from './Contact';
import { Route, Link, NavLink, BrowserRouter } from 'react-router-dom';
import HomeIcon from '@material-ui/icons/Home';
import PersonPinIcon from '@material-ui/icons/PersonPin';
import EmailIcon from '@material-ui/icons/Email';
import Button from '@material-ui/core/Button';



function NavBar(props) {

    return (
        <List component="nav">
            <ListItem component="div">
                <ListItemText inset>
                    <TypoGraphy color="inherit" variant="title">
                        <NavLink to='/' class='navlink1'><Button>Home</Button></NavLink>
					</TypoGraphy>
                </ListItemText>
                <ListItemText inset>
                    <TypoGraphy color="inherit" variant="title">
                        <NavLink to='/About' class='navlink2'><Button>About</Button></NavLink>
					</TypoGraphy>
                </ListItemText>
                <ListItemText inset>
                    <TypoGraphy color="inherit" variant="title">
                        <NavLink to='/Contact' class='navlink3'><Button>Contact</Button></NavLink>
					</TypoGraphy>
                </ListItemText>
            </ListItem >

        </List>
    )
}


export default NavBar;