import React from 'react';
import '../App.css';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';

const About = () => (
	<div class='about-div'>
		<Grid containter direction='column' spacing={1} class='about-grid'>
			<Card raised={true}>			
				<Grid item style={{marginBottom:'1em'}}>
					<CardHeader title={'About'} titleTypographyProps={{variant:'h5', gutterBottom:false}} style={{backgroundColor:'#0073E6', color:'#FFFFFF'}}>About</CardHeader>
						<CardContent style={{paddingLeft:5, paddingRight:5, paddingBottom:2}}>
							<h2>Experience</h2>
								<p>Benjamin Zook is a second year graduate student at the University of Cincinnati's College of Engineering and Applied Sciences. He is completing a Master's of Engineering degree in Computer Engineering that is focused on Data Science and machine techniques. </p>
							<h2>Education</h2>
								<p>Mr. Zook worked in the field of Applied Behavioral Analysis as a therapist for over ten years, before earning a degree in Computer Science from Wright State University.</p>
						</CardContent>
				</Grid>
			</Card>
		</Grid>
	</div>
);

export default About;