import React from 'react';
import '../App.css';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';

const Contact = () => (
	<div class='contacts-div'>		
		<Grid container direction='row' spacing={1} justify={'left'}>	
			<Grid item xs={10}>
				<Card raised={true}>
					<CardHeader title={'Contact'} titleTypographyProps={{variant:'h5', gutterBottom:false}} style={{backgroundColor:'#0073E6', color:'#FFFFFF'}}>Contact</CardHeader>
						<CardContent style={{paddingLeft:10, paddingRight:5, paddingBottom:5}}> 
							<Grid item>
								<h2>Email</h2>
									<a href='mailto:[zookbn@mail.uc.edu]'><MailOutlineIcon/></a>
							</Grid>						
							<Grid item>
								<h2>LinkedIn</h2>
								<a href='https://www.linkedin.com/in/benjamin-z-307113100/'><p><LinkedInIcon/></p></a>
							</Grid>								
							<Grid item>
								<h2>Github</h2>
									<a href='https://github.com/git-ben18'><GitHubIcon/></a>
							</Grid>						
					</CardContent>
				</Card>
			</Grid>
		</Grid>
	</div>
);

export default Contact;